        <!--  -->
        <ul class="headNavbar">
            <li><div class="line"></div><router-link to="/"></router-link></li>
            <li><div class="line"></div><router-link to="/collection"></router-link></li>
            <li><div class="line"></div><router-link to="/shoppingCart"></router-link></li>
            <li><div class="line"></div><router-link to="/orders"></router-link></li>
            <li><div class="line"></div><router-link to="/user"></router-link></li>
        </ul>
        <!--  -->
        <div class="headSearch">
            <div>
                <input type="text" placeholder="" autofocus v-model="inputContent" :style="input_style_width" @mouseover="inp_mouseover" @mouseout="inp_mouseout" @blur="inp_blur" /><button><span class="iconfont icon-sousuo"></span></button>
            </div>
