</template>
<style scoped>
    /* #cae2ee*/
    .header {
        background-color: #cae2ee;
        border-bottom: 2px solid tomato;
    }
    /* header */
    header {
        display: flex;
        height: 60px;
        padding-top: 10px;
        box-sizing: border-box;
        background-color: #cae2ee;
    }
    /* logo */
    .logo img {
        width: 80px;
        height: 40px;
        margin-left: 40px;
    }
    /*  */
    .headNavbar {
        display: flex;
        margin-left: 20px;
    }
    .headNavbar li {
        height: 38px;
    }
    .headNavbar .line {
        height: 2px;
        width: 0;
        background-color: tomato;
        margin: 0 auto;
        transition: all 0.3s;
        opacity: 0;
    }
    .headNavbar li:hover .line {
        width: 90%;
        opacity: 1;
    }
    .headNavbar a {
        line-height: 40px;
        margin: 0 10px;
        font-weight: 700;
        color: #666;
        transition: all 0.2s;
    }
    .headNavbar a:hover {
        color: tomato;
    }
    /*  */
    .headSearch {
        margin-left: 20px;
        width: 410px;
    }
    .headSearch>div {
        float: right;
    }
    .headSearch input {
        height: 40px;
        box-sizing: border-box;
        padding-left: 10px;
        font-size: 14px;
        border: 2px solid tomato;
        border-radius: 15px 0 0 15px;
        transition: all 0.5s;
    }
    .headSearch button {
        width: 60px;
        height: 40px;
        vertical-align: top;
        background-color: pink;
        border: 2px solid tomato;
        box-sizing: border-box;
        border-left: 0;
        color: #fff;
        border-radius: 0 15px 15px 0;
    }
    .headSearch button:hover span {
        color: tomato;
    }
    .headSearch button span {
        font-size: 24px;
        transition: all 0.2s;
    }
    /*  */
    .headUser {
        height: 30px;
        text-align: center;
        line-height: 30px;
        margin-top: 5px;
        box-sizing: border-box;
        font-size: 14px;
        margin-left: 60px;
    }
    .headUser a {
        color: tomato;
        font-weight: 600;
    }
    .headUser span:nth-child(3) {
        color: thistle;
    }
    .greetings {
        font-size: 12px;
        padding-right: 10px;
        font-weight: 600;
        font-family: MingLiU;
    }
</style>
<script>
export default {
    data() {
        return {
            inputContent: '',  //inputvalue
            input_style_width:'width: 200px;'//inputstyle
        }
    },
    methods: {
//-----------------------inputwidth-----------------------------

        //inp300px;
        inp_mouseover() {
            this.input_style_width='width: 300px;';
        },
        //inpvalueinp200px
        inp_mouseout() {
            if(!this.inputContent) this.input_style_width='width: 200px;';
        },
        //inpinpvalueinp200px
        inp_blur() {
            this.inp_mouseout();
        }
//----------------------inputwidth-----------------
    },
    watch: {
//-----------------------inputwidth-----------------------------
        inputContent() {
            //inputContent,300px;
            if(this.inputContent) this.input_style_width='width: 300px;';
//-----------------------inputwidth--------------------
        }
    }
}
</script>